@import url(https://fonts.googleapis.com/css?family=Muli&&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {
  color: inherit;
  text-decoration: none;
}

* {
  box-sizing: border-box;
}

h1,
h3 {
  margin: 0;
}

body {
  font-family: 'Muli', sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  overflow: hidden;
  margin: 0 auto;
  padding: 0.5em;
}

header {
  display: flex;
  align-items: center;
}

.tagline {
  display: flex;
  flex-direction: column;
  margin: 0 1em 0 0;
}

.tagline > h1 {
  flex: 1 1;
  font-size: 1.5em;
}

.tagline--credit {
  font-size: 12pt;
}

.tagline--credit > a {
  border-bottom: 1px dotted;
}

nav {
  display: flex;
  flex-wrap: wrap;
}

.nav-button {
  flex: 1 1;
  margin: 0.5em;
  padding: 0.5em 1.5em;
  background-color: black;
  border: none;
  color: #fff;
  cursor: pointer;
  transition-duration: 0.4s;
  outline: none;
}

.nav-button.active,
.nav-button:hover {
  background-color: #95e8fa;
}

main {
  display: flex;
}

.panel {
  background-size: auto 100%;
  background-position: center;
  background-repeat: no-repeat;
  height: 80vh;
  border-radius: 50px;
  color: #fff;
  cursor: pointer;
  flex: 0.5 1;
  margin: 10px;
  position: relative;
  transition: flex 0.7s ease-in;
}

.panel h3 {
  font-size: 24px;
  position: absolute;
  bottom: 1em;
  left: 2em;
  opacity: 0;
}

.panel.active {
  flex: 5 1;
  border-radius: 20px;
}

.panel.active h3 {
  opacity: 1;
  transition: opacity 0.3s ease-in 0.4s;
}

@media (max-width: 1024px) {
  body {
    height: auto;
  }

  header {
    display: flex;
    flex-direction: column;
  }

  .tagline--credit {
    text-align: center;
    margin-bottom: 1em;
  }

  .nav-button {
    margin: 0.25em;
    padding: 0.25em 1em;
  }

  .panel h3 {
    text-align: center;
    left: 0.5em;
  }
}

